	module.exports = {"pathContext":{"docHtml":"<h1>2.3.2 / 2017-09-09</h1>\n<ul>\n<li>\n<p>deps: iconv-lite@0.4.19</p>\n<ul>\n<li>Fix ISO-8859-1regression</li>\n<li>Update Windows-1255</li>\n</ul>\n</li>\n</ul>\n<h1>2.3.1 / 2017-09-07</h1>\n<ul>\n<li>deps: bytes@3.0.0</li>\n<li>\n<p>deps: http-errors@1.6.2</p>\n<ul>\n<li>deps: depd@1.1.1</li>\n</ul>\n</li>\n<li>perf: skip buffer decoding on overage chunk</li>\n</ul>\n<h1>2.3.0 / 2017-08-04</h1>\n<ul>\n<li>Add TypeScript definitions</li>\n<li>Use <code>http-errors</code> for standard emitted errors</li>\n<li>deps: bytes@2.5.0</li>\n<li>\n<p>deps: iconv-lite@0.4.18</p>\n<ul>\n<li>Add support for React Native</li>\n<li>Add a warning if not loaded as utf-8</li>\n<li>Fix CESU-8 decoding in Node.js 8</li>\n<li>Improve speed of ISO-8859-1 encoding</li>\n</ul>\n</li>\n</ul>\n<h1>2.2.0 / 2017-01-02</h1>\n<ul>\n<li>\n<p>deps: iconv-lite@0.4.15</p>\n<ul>\n<li>Added encoding MS-31J</li>\n<li>Added encoding MS-932</li>\n<li>Added encoding MS-936</li>\n<li>Added encoding MS-949</li>\n<li>Added encoding MS-950</li>\n<li>Fix GBK/GB18030 handling of Euro character</li>\n</ul>\n</li>\n</ul>\n<h1>2.1.7 / 2016-06-19</h1>\n<ul>\n<li>deps: bytes@2.4.0</li>\n<li>perf: remove double-cleanup on happy path</li>\n</ul>\n<h1>2.1.6 / 2016-03-07</h1>\n<ul>\n<li>\n<p>deps: bytes@2.3.0</p>\n<ul>\n<li>Drop partial bytes on all parsed units</li>\n<li>Fix parsing byte string that looks like hex</li>\n</ul>\n</li>\n</ul>\n<h1>2.1.5 / 2015-11-30</h1>\n<ul>\n<li>deps: bytes@2.2.0</li>\n<li>deps: iconv-lite@0.4.13</li>\n</ul>\n<h1>2.1.4 / 2015-09-27</h1>\n<ul>\n<li>Fix masking critical errors from <code>iconv-lite</code></li>\n<li>\n<p>deps: iconv-lite@0.4.12</p>\n<ul>\n<li>Fix CESU-8 decoding in Node.js 4.x</li>\n</ul>\n</li>\n</ul>\n<h1>2.1.3 / 2015-09-12</h1>\n<ul>\n<li>\n<p>Fix sync callback when attaching data listener causes sync read</p>\n<ul>\n<li>Node.js 0.10 compatibility issue</li>\n</ul>\n</li>\n</ul>\n<h1>2.1.2 / 2015-07-05</h1>\n<ul>\n<li>Fix error stack traces to skip <code>makeError</code></li>\n<li>\n<p>deps: iconv-lite@0.4.11</p>\n<ul>\n<li>Add encoding CESU-8</li>\n</ul>\n</li>\n</ul>\n<h1>2.1.1 / 2015-06-14</h1>\n<ul>\n<li>Use <code>unpipe</code> module for unpiping requests</li>\n</ul>\n<h1>2.1.0 / 2015-05-28</h1>\n<ul>\n<li>\n<p>deps: iconv-lite@0.4.10</p>\n<ul>\n<li>Improved UTF-16 endianness detection</li>\n<li>Leading BOM is now removed when decoding</li>\n<li>The encoding UTF-16 without BOM now defaults to UTF-16LE when detection fails</li>\n</ul>\n</li>\n</ul>\n<h1>2.0.2 / 2015-05-21</h1>\n<ul>\n<li>\n<p>deps: bytes@2.1.0</p>\n<ul>\n<li>Slight optimizations</li>\n</ul>\n</li>\n</ul>\n<h1>2.0.1 / 2015-05-10</h1>\n<ul>\n<li>Fix a false-positive when unpiping in Node.js 0.8</li>\n</ul>\n<h1>2.0.0 / 2015-05-08</h1>\n<ul>\n<li>Return a promise without callback instead of thunk</li>\n<li>\n<p>deps: bytes@2.0.1</p>\n<ul>\n<li>units no longer case sensitive when parsing</li>\n</ul>\n</li>\n</ul>\n<h1>1.3.4 / 2015-04-15</h1>\n<ul>\n<li>Fix hanging callback if request aborts during read</li>\n<li>\n<p>deps: iconv-lite@0.4.8</p>\n<ul>\n<li>Add encoding alias UNICODE-1-1-UTF-7</li>\n</ul>\n</li>\n</ul>\n<h1>1.3.3 / 2015-02-08</h1>\n<ul>\n<li>\n<p>deps: iconv-lite@0.4.7</p>\n<ul>\n<li>Gracefully support enumerables on <code>Object.prototype</code></li>\n</ul>\n</li>\n</ul>\n<h1>1.3.2 / 2015-01-20</h1>\n<ul>\n<li>\n<p>deps: iconv-lite@0.4.6</p>\n<ul>\n<li>Fix rare aliases of single-byte encodings</li>\n</ul>\n</li>\n</ul>\n<h1>1.3.1 / 2014-11-21</h1>\n<ul>\n<li>\n<p>deps: iconv-lite@0.4.5</p>\n<ul>\n<li>Fix Windows-31J and X-SJIS encoding support</li>\n</ul>\n</li>\n</ul>\n<h1>1.3.0 / 2014-07-20</h1>\n<ul>\n<li>\n<p>Fully unpipe the stream on error</p>\n<ul>\n<li>Fixes <code>Cannot switch to old mode now</code> error on Node.js 0.10+</li>\n</ul>\n</li>\n</ul>\n<h1>1.2.3 / 2014-07-20</h1>\n<ul>\n<li>\n<p>deps: iconv-lite@0.4.4</p>\n<ul>\n<li>Added encoding UTF-7</li>\n</ul>\n</li>\n</ul>\n<h1>1.2.2 / 2014-06-19</h1>\n<ul>\n<li>Send invalid encoding error to callback</li>\n</ul>\n<h1>1.2.1 / 2014-06-15</h1>\n<ul>\n<li>\n<p>deps: iconv-lite@0.4.3</p>\n<ul>\n<li>Added encodings UTF-16BE and UTF-16 with BOM</li>\n</ul>\n</li>\n</ul>\n<h1>1.2.0 / 2014-06-13</h1>\n<ul>\n<li>Passing string as <code>options</code> interpreted as encoding</li>\n<li>Support all encodings from <code>iconv-lite</code></li>\n</ul>\n<h1>1.1.7 / 2014-06-12</h1>\n<ul>\n<li>use <code>string_decoder</code> module from npm</li>\n</ul>\n<h1>1.1.6 / 2014-05-27</h1>\n<ul>\n<li>check encoding for old streams1</li>\n<li>support node.js &#x3C; 0.10.6</li>\n</ul>\n<h1>1.1.5 / 2014-05-14</h1>\n<ul>\n<li>bump bytes</li>\n</ul>\n<h1>1.1.4 / 2014-04-19</h1>\n<ul>\n<li>allow true as an option</li>\n<li>bump bytes</li>\n</ul>\n<h1>1.1.3 / 2014-03-02</h1>\n<ul>\n<li>fix case when length=null</li>\n</ul>\n<h1>1.1.2 / 2013-12-01</h1>\n<ul>\n<li>be less strict on state.encoding check</li>\n</ul>\n<h1>1.1.1 / 2013-11-27</h1>\n<ul>\n<li>add engines</li>\n</ul>\n<h1>1.1.0 / 2013-11-27</h1>\n<ul>\n<li>add err.statusCode and err.type</li>\n<li>allow for encoding option to be true</li>\n<li>pause the stream instead of dumping on error</li>\n<li>throw if the stream's encoding is set</li>\n</ul>\n<h1>1.0.1 / 2013-11-19</h1>\n<ul>\n<li>dont support streams1, throw if dev set encoding</li>\n</ul>\n<h1>1.0.0 / 2013-11-17</h1>\n<ul>\n<li>rename <code>expected</code> option to <code>length</code></li>\n</ul>\n<h1>0.2.0 / 2013-11-15</h1>\n<ul>\n<li>republish</li>\n</ul>\n<h1>0.1.1 / 2013-11-15</h1>\n<ul>\n<li>use bytes</li>\n</ul>\n<h1>0.1.0 / 2013-11-11</h1>\n<ul>\n<li>generator support</li>\n</ul>\n<h1>0.0.3 / 2013-10-10</h1>\n<ul>\n<li>update repo</li>\n</ul>\n<h1>0.0.2 / 2013-09-14</h1>\n<ul>\n<li>dump stream on bad headers</li>\n<li>listen to events after defining received and buffers</li>\n</ul>\n<h1>0.0.1 / 2013-09-14</h1>\n<ul>\n<li>Initial release</li>\n</ul>","docPath":"argo-ci/node_modules/raw-body/history","proj":"argo-ci"}}

